<template>
  <div>
    <h4>简易protobuff解码工具</h4>
    选择编码的消息
    <select v-model="selected">
      <option
        v-for="option in options"
        v-bind:value="option.value"
        v-bind:key="option.value"
      >
        {{ option.text }}
      </option>
    </select>
    <br /><br />
    <div style="display: flex; flex-flow: row">
      <div style="display: flex; flex-flow: column; width: 50%; padding: 5px">
        <textarea
          v-model="pb"
          placeholder="输入pb定义"
          style="height: 400px; resize: none"
        ></textarea>
      </div>
      <div style="display: flex; flex-flow: column; width: 50%; padding: 5px">
        <textarea
          v-model="data"
          placeholder="输入数据JSON [1,2,3] 或 {0:1,1:2,2:3} 格式"
          style="height: 400px; resize: none"
        ></textarea>
      </div>
    </div>
    <h4>{{ decoded }}</h4>
  </div>
</template>

<script>
var protobuf = require("protobufjs");
const dumptype = (root, path, r) => {
  if (root.nested) {
    Object.values(root.nested).forEach((v) => {
      dumptype(v, root.name ? `${path}.${root.name}` : path, r);
    });
  } else {
    if (root.fields) {
      r.push({ text: root.name ? `${path}.${root.name}` : path, value: root });
    }
  }
};
export default {
  name: "pbtool",
  data() {
    return {
      pb: `package awesomepackage;
syntax = "proto3";

message AwesomeMessage {
    string awesome_field = 1; // becomes awesomeField
}`,
      data: '{"0":10,"1":5,"2":104,"3":101,"4":108,"5":108,"6":111}',
      selected: 0,
    };
  },
  computed: {
    decoded: function () {
      if (this.selected && this.data) {
        try {
          const data = JSON.parse(this.data);
          if (Array.isArray(data)) {
            const msgData = new Uint8Array(data);
            return decoder(msgData);
          } else {
            const msgData = new Uint8Array(Object.keys(data).length);
            Object.keys(data).forEach((k) => {
              msgData[k] = data[k];
            });
            return this.selected.decode(msgData);
          }
        } catch (e) {
          return e;
        }
      } else {
        return "左侧输入pb定义，右侧输入数据，顶部选择消息类型";
      }
    },
    options: function () {
      if (this.pb) {
        try {
          const r = protobuf.parse(this.pb);
          const result = [];
          dumptype(r.root, "", result);
          return result;
        } catch (e) {
          return e;
        }
      } else {
        return [{ text: "先输入消息定义", value: null }];
      }
    },
  },
};
</script>



